<template>
  <view class="container">
	<button @click="SynInformation" :disabled = "!canbutton"> ÂêåÊ≠•‰ø°ÊÅØ </button>
	<button @click="gameStart" :disabled = "!isConnected && !canbutton"> ÂºÄÂßãÊ∏∏Êàè </button>
    
    <!-- Âá∫ÁâåÊùÉÊòæÁ§∫ -->
    <view class="turn-section" v-if="gameStatus === 'playing' && currentPlayer">
      <view :class="['turn-indicator', isYourTurn ? 'your-turn' : 'other-turn']">
        <text v-if="isYourTurn" class="turn-text">üéÆ ËΩÆÂà∞ÊÇ®Âá∫Áâå</text>
        <text v-else class="turn-text">‚è≥ ËΩÆÂà∞Áé©ÂÆ∂ {{ currentPlayer }} Âá∫Áâå</text>
      </view>
    </view>
    
    <!-- ÁâåÂ†ÜÊòæÁ§∫ -->
    <view class="piles-section" v-if="gamePiles">
      <h3>ÁâåÂ†Ü</h3>
      <view class="piles-container">
        <view v-for="(pile, suit) in gamePiles" :key="suit" class="pile-item">
          <view :class="['pile', 'pile-' + suit]">
            <text class="pile-suit">{{ getSuitSymbol(suit) }}</text>
            <text class="pile-count">{{ pile.count }}Âº†</text>
            
            <!-- ÊòæÁ§∫ÂÆåÊï¥ÁöÑÁâåÂ†ÜÂ∫èÂàó -->
            <view class="pile-cards" v-if="pile.cards && pile.cards.length > 0">
              <view class="pile-sequence">
                <text class="pile-sequence-label">ÁâåÂ†ÜÂ∫èÂàó:</text>
                <view class="card-sequence">
                  <text 
                    v-for="(entry, index) in pile.cards" 
                    :key="index"
                    :class="['sequence-card', entry.card.rank === '7' ? 'seven-card' : '']"
                  >
                    {{ entry.card.rank }}{{ getSuitSymbol(entry.card.suit) }}
                    <span v-if="index < pile.cards.length - 1">‚Üí</span>
                  </text>
                </view>
              </view>
            </view>
            
            <text v-else class="pile-empty">Á©∫</text>
            <text v-if="pile.playedBy" class="pile-player">
              ÊúÄÂêéÂá∫Áâå: {{ pile.playedBy }}
            </text>
          </view>
        </view>
      </view>
    </view>
    
    <!-- Áé©ÂÆ∂ÂàóË°® -->
    <view class="player-section">
      <h3>Âú®Á∫øÁé©ÂÆ∂ ({{ playerlist.length }})</h3>
      <ul>
        <li v-for="(item, index) in playerlist" :key="index">
          Áé©ÂÆ∂{{ index+1 }}: {{ item.deviceId || item }}
        </li>
      </ul>
    </view>
    
    <!-- Áé©ÂÆ∂ÊâãÁâå -->
    <view class="cards-section" v-if="playerCards.length > 0">
      <h3>‰Ω†ÁöÑÊâãÁâå ({{ playerCards.length }}Âº†)</h3>
      
      <!-- ÈÄâ‰∏≠ÁöÑÁâå -->
      <view class="selected-section" v-if="selectedCard">
        <h4>Â∑≤ÈÄâÁâå</h4>
        <view class="selected-cards">
          <view class="card-item">
            <view :class="['card', 'card-' + selectedCard.suit, 'selected']">
              <text class="card-rank">{{ selectedCard.rank }}</text>
              <text class="card-suit">{{ getSuitSymbol(selectedCard.suit) }}</text>
            </view>
          </view>
        </view>
        <view class="action-buttons">
          <button @click="playCard" class="play-btn">Âá∫Áâå</button>
          <button @click="clearSelection" class="clear-btn">ÂèñÊ∂àÈÄâÊã©</button>
        </view>
        
        <!-- PassÊåâÈíÆ -->
        <view class="pass-section" v-if="isYourTurn && gameStatus === 'playing'">
          <button @click="passTurn" class="pass-btn" :disabled="!canPass">
            Pass
          </button>
          <text v-if="!canPass" class="pass-hint">
            {{ passHint }}
          </text>
        </view>
      </view>
      
      <!-- ÊâÄÊúâÊâãÁâå -->
      <view class="cards-container">
        <view v-for="(card, index) in playerCards" :key="index" class="card-item">
          <view 
            :class="['card', 'card-' + card.suit, selectedCard && selectedCard.id === card.id ? 'selected' : '']"
            @click="selectCard(card)"
          >
            <text class="card-rank">{{ card.rank }}</text>
            <text class="card-suit">{{ getSuitSymbol(card.suit) }}</text>
            <text v-if="selectedCard && selectedCard.id === card.id" class="selected-mark">‚úì</text>
          </view>
        </view>
      </view>
    </view>
    
  </view>
</template>

<script>


const app = getApp();
export default {
  
	data(){
		return{
			canbutton : false,
			isConnected : false ,
			playerlist:[],
			playerCards: [],
			selectedCard: null, // ÈÄâ‰∏≠ÁöÑÂçïÂº†Áâå
			gameStatus: 'waiting', // waiting, playing, ended
			gamePiles: {
				hearts: { suit: 'hearts', count: 0, topCard: null, playedBy: null, cards: [] },
				spades: { suit: 'spades', count: 0, topCard: null, playedBy: null, cards: [] },
				diamonds: { suit: 'diamonds', count: 0, topCard: null, playedBy: null, cards: [] },
				clubs: { suit: 'clubs', count: 0, topCard: null, playedBy: null, cards: [] }
			},
			currentPlayer: null, // ÂΩìÂâçÂá∫ÁâåÁé©ÂÆ∂
			isYourTurn: false, // ÊòØÂê¶ËΩÆÂà∞ÂΩìÂâçÁé©ÂÆ∂Âá∫Áâå
			canPass: false, // ÊòØÂê¶ÂèØ‰ª•pass
			passHint: '' // passÊèêÁ§∫‰ø°ÊÅØ
		}
	},
  onLoad(){
    this.setupWebSocketListener();
  },
  methods: {
    setupWebSocketListener() {
      if (app.globalData.socketTask && app.globalData.isConnected) {
		  console.log("ÁõëÂê¨Âô®ËÆæÁΩÆÊàêÂäü")
		  this.canbutton = true;
        // ËÆæÁΩÆÊ∂àÊÅØÁõëÂê¨Âô®
        app.globalData.socketTask.onMessage((res) => {
          console.log('HomeÈ°µÈù¢Êî∂Âà∞WebSocketÊ∂àÊÅØ:', res);
          
          try {
            // Ëß£ÊûêJSONÊ∂àÊÅØ
            const messageData = JSON.parse(res.data);
            if(messageData.type == 'syninformation'){
				console.log("ËøõÂÖ•Âà∞ÂêåÊ≠•Êï∞ÁªÑif")
              this.playerlist = messageData.content;
            }
         //----------ÂàÜÂâ≤ÊØè‰∏ÄÊù°Ê∂àÊÅØÂ§ÑÁêÜ
		 //Â§ÑÁêÜÂºÄÂßãÊ∏∏ÊàèÂêéÁöÑÊ∂àÊÅØÂ§ÑÁêÜ
			if(messageData.type == 'alert'){
				console.log(messageData.content)
				uni.showToast({
					title:messageData.content,
					icon:'error',
					duration:2000
				})
			}
			if(messageData.type == "gameStartRes"){
				console.log("Êî∂Âà∞Ê∏∏ÊàèÂºÄÂßãÂìçÂ∫îÔºåÊâãÁâå‰ø°ÊÅØ:", messageData.content);
				this.playerCards = messageData.content.playerCards || [];
				this.gameStatus = 'playing';
				this.selectedCard = null;
				// ÂàùÂßãÂåñÁâåÂ†Ü
				this.gamePiles = {
					hearts: { suit: 'hearts', count: 0, topCard: null, playedBy: null, cards: [] },
					spades: { suit: 'spades', count: 0, topCard: null, playedBy: null, cards: [] },
					diamonds: { suit: 'diamonds', count: 0, topCard: null, playedBy: null, cards: [] },
					clubs: { suit: 'clubs', count: 0, topCard: null, playedBy: null, cards: [] }
				};
				// ËÆæÁΩÆÂΩìÂâçÂá∫ÁâåÁé©ÂÆ∂
				this.currentPlayer = messageData.content.currentPlayer;
				this.isYourTurn = messageData.content.isYourTurn;
				// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Pass
				this.checkCanPass();
				
				uni.showToast({
					title: `Ê∏∏ÊàèÂºÄÂßãÔºÅËé∑Âæó${this.playerCards.length}Âº†Áâå`,
					icon: 'success',
					duration: 2000
				});
			}
			
			// Â§ÑÁêÜÁâåÂ†ÜÊõ¥Êñ∞Ê∂àÊÅØ
			if(messageData.type == "pileUpdate"){
				console.log("Êî∂Âà∞ÁâåÂ†ÜÊõ¥Êñ∞Ê∂àÊÅØ:", messageData.content);
				this.gamePiles = messageData.content.pileInfo;
				
				// Êõ¥Êñ∞ÂΩìÂâçÂá∫ÁâåÁé©ÂÆ∂
				if(messageData.content.currentPlayer) {
					this.currentPlayer = messageData.content.currentPlayer;
					this.isYourTurn = this.currentPlayer === app.globalData.diviceid;
					// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Pass
					this.checkCanPass();
				}
				
				// Êõ¥Êñ∞Áé©ÂÆ∂ÊâãÁâåÊï∞Èáè
				if(messageData.content.remainingCards !== undefined) {
					// Â¶ÇÊûúÊòØÂΩìÂâçÁé©ÂÆ∂ÁöÑÂá∫ÁâåÔºåÊõ¥Êñ∞ÊâãÁâå
					if(messageData.content.playedBy === app.globalData.diviceid) {
						this.playerCards = this.playerCards.filter(card => 
							card.id !== messageData.content.playedCard.id
						);
						this.selectedCard = null;
					}
				}
			}
			
			// Â§ÑÁêÜÂá∫ÁâåÊàêÂäüÊ∂àÊÅØ
			if(messageData.type == "playCardSuccess"){
				console.log("Âá∫ÁâåÊàêÂäü:", messageData.content);
				uni.showToast({
					title: messageData.content,
					icon: 'success',
					duration: 2000
				});
			}
			
			// Â§ÑÁêÜÂá∫ÁâåÂ§±Ë¥•Ê∂àÊÅØ
			if(messageData.type == "playCardFail"){
				console.log("Âá∫ÁâåÂ§±Ë¥•:", messageData.content);
				uni.showToast({
					title: messageData.content,
					icon: 'error',
					duration: 2000
				});
			}
			
			// Â§ÑÁêÜPassÊàêÂäüÊ∂àÊÅØ
			if(messageData.type == "passSuccess"){
				console.log("PassÊàêÂäü:", messageData.content);
				
				// Êõ¥Êñ∞ÂΩìÂâçÂá∫ÁâåÁé©ÂÆ∂
				if(messageData.content.nextPlayer) {
					this.currentPlayer = messageData.content.nextPlayer;
					this.isYourTurn = this.currentPlayer === app.globalData.diviceid;
					// Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Pass
					this.checkCanPass();
				}
				
				uni.showToast({
					title: messageData.content.message,
					icon: 'success',
					duration: 2000
				});
			}
			
			// Â§ÑÁêÜPassÂ§±Ë¥•Ê∂àÊÅØ
			if(messageData.type == "passFail"){
				console.log("PassÂ§±Ë¥•:", messageData.content);
				uni.showToast({
					title: messageData.content,
					icon: 'error',
					duration: 2000
				});
			}
		 
		  } catch (error) {
            console.error('Ê∂àÊÅØËß£ÊûêÂ§±Ë¥•:', error, 'ÂéüÂßãÊï∞ÊçÆ:', res.data);
            // Â§ÑÁêÜÈùûJSONÊ†ºÂºèÊ∂àÊÅØ
            
          }
        });
        
        this.isListening = true;
        console.log('HomeÈ°µÈù¢WebSocketÁõëÂê¨Âô®Â∑≤ËÆæÁΩÆ');
      } else {
        console.log('WebSocketÊú™ËøûÊé•ÔºåÊó†Ê≥ïËÆæÁΩÆÁõëÂê¨Âô®');
        // ÂèØ‰ª•ËÆæÁΩÆÂÆöÊó∂Âô®ÈáçËØï
        setTimeout(() => {
          this.setupWebSocketListener();
        }, 2000);
      }
    },
    SynInformation(){
		console.log("ÊòØÂê¶ÊàêÂäüËøûÊé•" + app.globalData.isConnected);
		console.log("socketTask:" + app.globalData.socketTask);
      if (app.globalData.isConnected && app.globalData.socketTask) {
					const message = {
						type: 'system',
						reqmethoud : 'syninformation',
						playerid : app.globalData.diviceid,
            
					};
					
					app.globalData.socketTask.send({
						data: JSON.stringify(message),
						success: () => {
							//----------Âú®ËøûÊé•ÊàêÂäüÁöÑÂõûË∞É‰∏≠ËÆæÁΩÆÁõëÂê¨ÂáΩÊï∞-------------------------
							this.isConnected = true
							console.log('ÂêåÊ≠•‰ø°ÊÅØÊàêÂäü');
							
							
						},
						fail: (err) => {
							console.error('ÂêåÊ≠•Áî®Êà∑‰ø°ÊÅØÂ§±Ë¥•:', err);
							
						}
					});
				} else {
					uni.showToast({
						title: 'WebSocketÊú™ËøûÊé•',
						icon: 'none'
					});
				}
    },
    gameStart(){
		const message = {
			type: 'gameStart',
			reqmethoud : 'user',
			playerid : app.globalData.diviceid,
		            };
			app.globalData.socketTask.send({
				data: JSON.stringify(message),
				success: () => {
					//----------Âú®ËøûÊé•ÊàêÂäüÁöÑÂõûË∞É‰∏≠ËÆæÁΩÆÁõëÂê¨ÂáΩÊï∞-------------------------
					this.isConnected = true
					console.log('ÂèëÈÄÅÂºÄÂßãÊ∏∏ÊàèËØ∑Ê±Ç');
					
					
				},
				fail: (err) => {
					console.error('ÂèëÈÄÅÂç°ÂºÄÂßãËØ∑Ê±ÇÂ§±Ë¥•:', err);
							
				}
			});
		
	},
    getSuitSymbol(suit) {
      switch(suit) {
        case 'hearts': return '‚ô•';
        case 'spades': return '‚ô†';
        case 'clubs': return '‚ô£';
        case 'diamonds': return '‚ô¶';
        default: return suit;
      }
    },
    // ÈÄâÊã©ÂçïÂº†Áâå
    selectCard(card) {
      if (this.selectedCard && this.selectedCard.id === card.id) {
        // Â¶ÇÊûúÁÇπÂáªÁöÑÊòØÂ∑≤ÈÄâ‰∏≠ÁöÑÁâåÔºåÂàôÂèñÊ∂àÈÄâÊã©
        this.selectedCard = null;
      } else {
        // ÈÄâÊã©Êñ∞ÁöÑÁâå
        this.selectedCard = card;
      }
    },
    // ÂèñÊ∂àÈÄâÊã©
    clearSelection() {
      this.selectedCard = null;
    },
    // Âá∫Áâå
    playCard() {
      if (!this.selectedCard) {
        uni.showToast({
          title: 'ËØ∑ÂÖàÈÄâÊã©Ë¶ÅÂá∫ÁöÑÁâå',
          icon: 'none',
          duration: 2000
        });
        return;
      }
      
      console.log('Âá∫Áâå:', this.selectedCard);
      
      // ÂèëÈÄÅÂá∫ÁâåÊ∂àÊÅØÂà∞ÊúçÂä°Âô®
      const message = {
        type: 'playCard',
        playerid: app.globalData.diviceid,
        card: this.selectedCard,
		
      };
      
      app.globalData.socketTask.send({
        data: JSON.stringify(message),
        success: () => {
          console.log('Âá∫ÁâåËØ∑Ê±ÇÂèëÈÄÅÊàêÂäüÔºåÁ≠âÂæÖÊúçÂä°Âô®È™åËØÅ');
          // ‰∏çÂÜçÁ´ãÂç≥ÁßªÈô§ÊâãÁâåÔºåÁ≠âÂæÖÊúçÂä°Âô®Á°ÆËÆ§
          // ÊâãÁâåÁßªÈô§ÂíåÁâåÂ†ÜÊõ¥Êñ∞Â∞ÜÂú®Êî∂Âà∞ÊúçÂä°Âô®Á°ÆËÆ§Ê∂àÊÅØÂêéÂ§ÑÁêÜ
        },
        fail: (err) => {
          console.error('Âá∫ÁâåËØ∑Ê±ÇÂèëÈÄÅÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'Âá∫ÁâåËØ∑Ê±ÇÂèëÈÄÅÂ§±Ë¥•',
            icon: 'none',
            duration: 2000
          });
        }
      });
    },
    
    // PassÊìç‰Ωú
    passTurn() {
      if (!this.isYourTurn) {
        uni.showToast({
          title: 'Áé∞Âú®‰∏çÊòØ‰Ω†ÁöÑÂõûÂêà',
          icon: 'none',
          duration: 2000
        });
        return;
      }
      
      console.log('Áé©ÂÆ∂ÈÄâÊã©Pass');
      
      // ÂèëÈÄÅPassÊ∂àÊÅØÂà∞ÊúçÂä°Âô®
      const message = {
        type: 'passTurn',
        playerid: app.globalData.diviceid
      };
      
      app.globalData.socketTask.send({
        data: JSON.stringify(message),
        success: () => {
          console.log('PassËØ∑Ê±ÇÂèëÈÄÅÊàêÂäü');
        },
        fail: (err) => {
          console.error('PassËØ∑Ê±ÇÂèëÈÄÅÂ§±Ë¥•:', err);
          uni.showToast({
            title: 'PassËØ∑Ê±ÇÂèëÈÄÅÂ§±Ë¥•',
            icon: 'none',
            duration: 2000
          });
        }
      });
    },
    
    // Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Pass
    checkCanPass() {
      if (!this.isYourTurn || this.gameStatus !== 'playing') {
        this.canPass = false;
        this.passHint = '';
        return;
      }
      
      // Ê£ÄÊü•ÊòØÂê¶Êúâ7Âú®ÊâãÁâå‰∏≠
      const hasSeven = this.playerCards.some(card => card.rank === '7');
      
      // Ê£ÄÊü•ÊòØÂê¶ÊúâÂèØ‰ª•Âá∫ÁöÑÁâå
      const canPlayAnyCard = this.checkCanPlayAnyCard();
      
      if (hasSeven) {
        // Êúâ7Âú®ÊâãÁâå‰∏≠Ôºå‰∏çËÉΩPass
        this.canPass = false;
        this.passHint = 'Êâã‰∏≠Êúâ7ÔºåÂøÖÈ°ªÂÖàÂá∫7';
      } else if (canPlayAnyCard) {
        // ÊúâÂèØ‰ª•Âá∫ÁöÑÁâåÔºåÂèØ‰ª•Pass‰ΩÜÊèêÁ§∫
        this.canPass = true;
        this.passHint = 'ÊúâÁâåÂèØÂá∫ÔºåÁ°ÆÂÆöË¶ÅPassÂêóÔºü';
      } else {
        // Ê≤°ÊúâÂèØ‰ª•Âá∫ÁöÑÁâåÔºåÂèØ‰ª•Pass
        this.canPass = true;
        this.passHint = 'Ê≤°ÊúâÂèØ‰ª•Âá∫ÁöÑÁâå';
      }
    },
    
    // Ê£ÄÊü•ÊòØÂê¶ÊúâÂèØ‰ª•Âá∫ÁöÑÁâå
    checkCanPlayAnyCard() {
      for (const card of this.playerCards) {
        // Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Âá∫Âà∞ÂØπÂ∫îËä±Ëâ≤ÁöÑÁâåÂ†Ü
        if (this.canPlayCardToPile(card)) {
          return true;
        }
      }
      return false;
    },
    
    // Ê£ÄÊü•ÂçïÂº†ÁâåÊòØÂê¶ÂèØ‰ª•Âá∫Âà∞ÁâåÂ†Ü
    canPlayCardToPile(card) {
      const pile = this.gamePiles[card.suit];
      
      if (!pile) return false;
      
      // Â¶ÇÊûúÁâåÂ†Ü‰∏∫Á©∫ÔºåÂè™ËÉΩÂá∫7
      if (pile.count === 0) {
        return card.rank === '7';
      }
      
      // Ëé∑ÂèñÁâåÂ†ÜÁöÑÈòüÂ∞æÂíåÈòüÂ§¥
      const tailCard = pile.cards[pile.cards.length - 1]?.card;
      const headCard = pile.cards[0]?.card;
      
      if (!tailCard || !headCard) return false;
      
      const cardValue = this.getCardValue(card.rank);
      const tailCardValue = this.getCardValue(tailCard.rank);
      const headCardValue = this.getCardValue(headCard.rank);
      
      // Ê£ÄÊü•ÊòØÂê¶ÂèØ‰ª•Êé•Âú®ÈòüÂ∞æÔºàÂêë‰∏äÊé•ÈæôÔºâÊàñÈòüÂ§¥ÔºàÂêë‰∏ãÊé•ÈæôÔºâ
      const canPlayToTail = Math.abs(cardValue - tailCardValue) === 1;
      const canPlayToHead = Math.abs(cardValue - headCardValue) === 1;
      
      return canPlayToTail || canPlayToHead;
    },
    
    // Ëé∑ÂèñÁâåÈù¢ÂÄº
    getCardValue(rank) {
      switch(rank) {
        case 'A': return 1;
        case 'J': return 11;
        case 'Q': return 12;
        case 'K': return 13;
        default: return parseInt(rank);
      }
    }
    // goToTest() {
    //   uni.navigateTo({
    //     url: '/pages/index/index'
    //   });
    // }
  }
}
</script>

